<template>
  <div class="content-printSet">
    <headerUsually btnShow="true" titleText="打印设置" url="/mobileOrdering/home"></headerUsually>
    <div class="list">
      <div class="item">
        <div>IP地址</div>
        <input class="input" type="text" v-model="ip" placeholder="请输入IP地址" />
      </div>
      <div class="item">
        <div>端口号</div>
        <input class="input" type="text" v-model="port" placeholder="请输入端口号" />
      </div>
    </div>
    <div class="btn" @click="printFun()">确定</div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
export default {
  components: {
    headerUsually
  },
  data() {
    return {
      ip: "",
      port: ""
    };
  },
  created() {
    let dcgateway = localStorage.getItem("dcgateway")
      ? JSON.parse(localStorage.getItem("dcgateway"))
      : {};
    this.ip = dcgateway.printIp;
    this.port = dcgateway.printPort;
  },
  methods: {
    async printFun() {
      let ipEXP = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
      if (!ipEXP.test(this.ip.trim())) {
        this.msg("IP地址不正确");
        return;
      }
      let portEXP = /^([0-9]|[1-9]\d|[1-9]\d{2}|[1-9]\d{3}|[1-5]\d{4}|6[0-4]\d{3}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/;
      if (!portEXP.test(this.port.trim())) {
        this.msg("端口号不正确");
        return;
      }

      let dcgateway = localStorage.getItem("dcgateway")
        ? JSON.parse(localStorage.getItem("dcgateway"))
        : {};
      dcgateway.printIp = this.ip.trim();
      dcgateway.printPort = this.port.trim();
      localStorage.setItem("dcgateway", JSON.stringify(dcgateway));
      this.msg("保存成功！");
      this.$router.push("/mobileOrdering/home");
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less" scoped>
.content-printSet {
  padding-top: 46px;
  .list {
    background: #ffffff;
    padding-left: 15px;
    margin-bottom: 20px;
    font-size: 14px;
    color: #666666;

    .item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #ddd;
      padding: 17px 15px 17px 0;
      .input {
        flex: 1;
        text-align: center;
        outline: none;
        border: none;
      }
    }
    .item:last-child {
      border-bottom: none;
    }
  }
  .btn {
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



